import React, { Component } from "react";
import "./section.component.css";

class Section extends Component {
    render() {
        return (
            <div className="marketing-form__section">
                <span className="marketing-form__section__label" data-step={this.props.step}>
                    {this.props.label}
                </span>
                <div className="marketing-form__section__content">{this.props.children}</div>
            </div>
        );
    }
}

export default Section;



